}

.info-card-area {
  height: inherit !important;
}

.info-card-area:not(.show) {
  display: block;
}

.info-card-area:not(.show):after {
  content: '';
  position: relative;
  display: block;
  margin-top: -150px;
  height: 150px;
  width: 100%;
  background-image: linear-gradient(to bottom, rgba(245, 245, 245, 0) 0, #F5F5F5 100%);
  margin-bottom: 10px;
}

.info-card-area .info-card-block {
  position: relative;
  height: 60vh;
  overflow: hidden;
  transition-property: all;
  transition-timing-function: ease-in-out;
  transition-timing-function: cubic-bezier(0.42, 0, 0.58, 1);
  transition: 0.2s;
}

.info-card-area.collapsing {
  height: inherit;
  overflow: auto;
  transition: all 0.35s ease;
}

.info-card-area.show .info-card-block {
  height: inherit;
}

.info-card-area.read-more-wrapper .info-card-block {
  height: 0;
}

.info-card-area.read-more-wrapper:not(.show):after {
  display: none;
}

.info-card-area.read-more-wrapper.show .info-card-block {
  height: 100%;
  overflow: visible;
}

.info-card-area.read-more-wrapper.show + .read-more-trigger .collapse-action-btn:before {
  content: 'Read Less';
}

.info-card-area.read-more-wrapper + .read-more-trigger .collapse-action-btn:before {
  content: 'Read More';
}

.collapse-action-btn {
  display: block;
  text-align: center;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #ED9524;
  transform: rotate(180deg);
  transition: all 0.25s ease;
}

.collapse-action-btn:hover {
  text-decoration: none;
}

.info-card-area.show + .collapse-action-btn {
  transform: rotate(0deg);
}

.info-card-area.read-more-wrapper + .read-more-trigger .collapse-action-btn {
  transform: rotate(0deg);
}

.read-more-trigger {
  text-align: center;
}

.read-more-trigger .collapse-action-btn {
  color: #e55a05;
  font-family: "Trump Gothic Pro", SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  letter-spacing: 1px;
  cursor: pointer;
  position: relative;
  z-index: 2;
  display: inline-block;
}

